import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { AlertController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { UtilsProvider } from '../../providers/utils/utils';

/**
 * Generated class for the ProductToPayBillPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-product-to-pay-bill',
  templateUrl: 'product-to-pay-bill.html',
})
export class ProductToPayBillPage {

  public cuentaPayBill: string;
  public selectedcuentaPayBill: string;
  //public flag : number = 0;
  public x: any;
  public valor: number;
  public cuenta: string = "";
  cuentaInfo: string = "";
  accountSplit: any;
  public param: any;
  private PAY_BILL:string ='payBill';
  public flagCuentaAsociada: number;

  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, public menu: MenuController, private alertCtrl: AlertController,
              public translate: TranslateService, public utils: UtilsProvider) {
    //Inhabilitar el menu lateral izquierdo
    this.menu.enable(false);

    this.storage.get('cuentaPayBill').then((val) =>{
        this.cuentaPayBill = val;
    });

   /* this.storage.get('accountSelectedTopay').then((val) => {
          if (val != null) {
            //this.selectedcuentaPayBill = val[1];
            this.selectedcuentaPayBill = val;
          }
    });*/

    //obtencion de la bandera enviada desde bills to pay para poner el numero 
    //de cuenta asociado a la factura
    this.flagCuentaAsociada =  this.navParams.get("flagCuentaAsociada"); 
  }
  
  ionViewDidLoad() { }

  ionViewDidEnter() {

    if(this.flagCuentaAsociada == 1){
      
      //Obtención del numero de la cuenta asociada a la factura
      this.storage.get('cuentaAsocFactSelec').then((val) => {
        if (val != null) {
          this.cuenta = val;
        }
      });
    }
    else if(this.flagCuentaAsociada == 2){
      
      this.storage.remove('cuentaAsocFactSelec');
      //Obtención por parametro de toda la cuenta seleccionada en product to debit
      this.storage.get('accountSelectedTopay').then((val) => {
        if (val != null) {
          this.cuenta = val;
        }
      });
    }

    /*
    //Obtención por parametro de toda la cuenta seleccionada
      this.storage.get('accountSelectedTopay').then((val) => {
        if (val != null) {
          this.cuenta = val;
        }
      });
    */

    //Obtención por parametro la cuenta seleccionada en product to debit
    this.storage.get('infoCuenta2').then((val) => {
      if (val != null) {
        //alert("val->"+val);
        this.cuentaInfo = val;
        //alert("val->"+this.cuenta2);
       
      }
    });
    
  }

  cancel(){
    //this.utils.cleanAll();
    this.storage.remove("accountSelectedTopay");
    this.navCtrl.push("RutaPagosPage");
  }

  //Metodo para ir a la pagina product to debit
  productToDebit(){
    this.navCtrl.push("ProductToDebitPage",{flag: this.PAY_BILL});
  }

  //Metodo del alertController para obtener info de los property
  presentAlert() {
    var alertTitle;
    var aceptar;
    var cancelar;
    this.translate.get('productToPayBill.mensaje').subscribe(
      value => {
        alertTitle = value;
      }        
    )
    //Property del boton aceptar
    this.translate.get('productToPayBill.aceptar').subscribe(
      value => {
        aceptar = value;
      }        
    )
    //Property del boton cancelar 
    this.translate.get('productToPayBill.cancelar').subscribe(
      value => {
        cancelar = value;
      }        
    )

    let alert = this.alertCtrl.create({
      title: alertTitle,
      buttons: [ 'OK'      
      ]
    });
    alert.present();
  }

  payment(){
   
    if(this.valor != undefined && this.cuenta != null){
      /*
      this.navCtrl.push("ConfirmPage",{payee: this.cuenta,
                                       account: this.split[1]+" - "+this.split[2],
                                       valor:this.valor });*/
     
      this.navCtrl.push("ConfirmPage",{payee: this.cuentaPayBill,
                                       account: this.cuenta,
                                       accountInfo: this.cuentaInfo,
                                       valor:this.valor,
                                       flag: this.PAY_BILL });
                                       
    }
    else{
      //alert("debe seleccionar la cuenta y el valor");
      //this.presentAlert();
      this.validarCamposVacios();
    }
       

  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }

  //Metodo de validación de campos - SOLO EN PARA MOSTRAR LOS ALERT Y LAS RAYAS ROJAS
  public validarCamposVacios(){
    
    if(this.cuenta == null || this.cuenta == "" && this.valor == undefined){
      this.utils.presentAlert('productToPay.validateCampos');
      this.colorAmountToPay();
      this.colorSelectedAccount();
    }
    else if(this.cuenta == null || this.cuenta == ""){
      this.colorSelectedAccount();
      this.utils.presentAlert('productToPay.validateAccount');
    }
    else if(this.valor == undefined){
      this.colorAmountToPay();
      this.utils.presentAlert('productToPay.validateAmount');
      
    }
  }

   //se traza la raya roja del account from
   public colorSelectedAccount(){
    document.getElementById("seleccionCuenta").style.borderBottom = "solid 1px red";
  }

  //se traza la raya roja del amount
  public colorAmountToPay(){
    document.getElementById("valorMonto").style.borderBottom = "solid 1px red";
  }

}



